import { NgModule } from '@angular/core';
import { IonicPageModule } from 'ionic-angular';
import { ElencoSitiPage } from './elenco-siti';

import { DashboardSitoPage } from '../dashboard-sito/dashboard-sito'

import { IonicPage, NavController, NavParams } from 'ionic-angular';

@NgModule({
  declarations: [
    ElencoSitiPage,
  ],
  imports: [
    IonicPageModule.forChild(ElencoSitiPage),
  ],
})
export class ElencoSitiPageModule {

  constructor(public navCtrl: NavController){}

  goToDetails(sito: any): void{
    this.navCtrl.push(DashboardSitoPage, {sito : sito})
  }
}



